import { row, col, img, css, inHtmlCode, stringToImg } from '../utils.js';

class Block {
  constructor(value, options) {
    this.value = value;
    this.options = options;
  }

  toHtml() {
    throw new Error('–ú–µ—Ç–æ–¥ toHtml –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å —Ä–µ–∞–ª–∏–∑–æ–≤–∞–Ω')
  }
}

class TitleBlock extends Block {
  constructor(value, options) {
    super(value, options);
  }

  toHtml() {
    const { tag = 'h1', styles } = this.options;
    return row(col(inHtmlCode(this.value, styles, tag)));
  }
}

class TextBlock extends Block {
  constructor(value, options) {
    super(value, options);
  }

  toHtml() {
    const { tag = 'p', styles } = this.options;
    return row(col(inHtmlCode(this.value, styles, tag)));
  }
}


/*  –ë–ª–æ–∫ —Ä–∞–±–æ—Ç–∞–µ—Ç —Å –º–∞—Å—Å–∏–≤–æ–º –¥–∞–Ω–Ω—ã—Ö, 
–µ—Å–ª–∏ –≤ value= —Å—Ç—Ä–æ–∫–∞ —Ç–æ —Ä–∞–∑–¥–µ–ª–∏—Ç–µ–ª—å –¥–ª—è –º–∞—Å—Å–∏–≤–∞ = "," */
class ColumnsBlock extends Block {
  constructor(value, options) {
    super(value, options);
  }

  toHtml() {
    const { tag = 'h1', styles } = this.options;

    // –µ—Å–ª–∏ –ø—Ä–∏—à–µ–ª –Ω–µ –º–∞—Å—Å–∏–≤ —Å –∫–æ–ª–æ–Ω–∫–∞–º–∏ –∞ —Å—Ç—Ä–æ–∫–∞ 
    // –ø—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞—Ç—å —Å—Ç—Ä–æ–∫—É –≤ –º–∞—Å—Å–∏–≤
    const valueForColumns = (typeof this.value === 'string')
      ? this.value.split(',')
      : this.value;

    // –µ—Å–ª–∏ –ø—Ä–∏—à–ª–æ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ —Ç–æ–≥–¥–∞ 
    // —Å–æ–∑–¥–∞–µ–º –æ–±—ä–µ–∫—Ç –¥–ª—è –∫–∞—Ä—Ç–∏–Ω–∫–∏
    // –∏ –ø–µ—Ä–µ–¥–∞–µ–º –≤ —Ñ-—é img –¥–ª—è –ø–æ–¥–≥–æ—Ç–æ–≤–∫–∏ html –∫–æ–¥–∞ –¥–ª—è –∫–∞—Ä—Ç–∏–Ω–∫–∏
    function StrOrImgToHtml(blockValue) {
      // –ø–µ—Ä–µ–¥–∞–ª–∏ —Ç–µ–∫—Å—Ç –∞ –Ω–µ —Å—Å—ã–ª–∫—É –Ω–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ
      if (blockValue.search('http') === -1) {
        // –µ—Å–ª–∏ —Å—Ç—Ä–æ–∫–∞ - –≤–µ—Ä–Ω—É—Ç—å –µ–µ –∂–µ
        return blockValue;
      }

      // –µ—Å–ª–∏ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ
      const image = stringToImg(blockValue, styles);
      return img(image);
    }

    const codeHtml = (blockValue) => inHtmlCode(StrOrImgToHtml(blockValue), styles, tag);
    const html = valueForColumns.map(blockValue => col(codeHtml(blockValue))).join('');

    return row(html);
  }
}

class ImageBlock extends Block {
  constructor(value, options) {
    super(value, options);
  }

  toHtml() {
    return row(img(this));
  }
}

export const Blocks = {
  ImageBlock,
  ColumnsBlock,
  TextBlock,
  TitleBlock,
}